$screen-xl: 90rem; // 1440px
$screen-l: 64rem; // 1024px
$screen-m: 48rem; // 768px
$screen-s: 26.25rem; // 420px

@font-face {
  font-family: AdidasFont;
  src: url(../fonts/AdihausDIN.ttf);
}

@mixin respond-xl {
  @media (max-width: $screen-xl) {
    @content;
  }
}

@mixin respond-l {
  @media (max-width: $screen-l) {
    @content;
  }
}

@mixin respond-m {
  @media (max-width: $screen-m) {
    @content;
  }
}

@mixin respond-s {
  @media (max-width: $screen-s) {
    @content;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: AdidasFont, "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0.01rem;
}

body {
  width: 100%;
  padding: 1rem 0;
}

main {
  width: min(90%, $screen-xl);
  margin-inline: auto;
}

#bar-chart-container {
  width: 100%;
  aspect-ratio: 2 / 1;
  margin-bottom: 1rem;
}

.pie-charts-container {
  width: 100%;
  // height: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 1rem;

  @include respond-m() {
    grid-template-columns: 1fr;
    width: 50%;
    margin-inline: auto;
  }

  #pie-chart-container-nbbikes,
  #pie-chart-container-nbemptydocks,
  #pie-chart-container-nbdocks {
    margin-inline: auto;
    width: 100%;
    aspect-ratio: 1 / 1;
  }
}

