@font-face {
  font-family: AdidasFont;
  src: url("AdihausDIN.073413d5.ttf");
}

* {
  box-sizing: border-box;
  letter-spacing: .01rem;
  margin: 0;
  padding: 0;
  font-family: AdidasFont, Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

body {
  width: 100%;
  padding: 1rem 0;
}

main {
  width: min(90%, 90rem);
  margin-inline: auto;
}

#bar-chart-container {
  aspect-ratio: 2;
  width: 100%;
  margin-bottom: 1rem;
}

.pie-charts-container {
  grid-template-columns: repeat(3, 1fr);
  column-gap: 1rem;
  width: 100%;
  display: grid;
}

@media (width <= 48rem) {
  .pie-charts-container {
    grid-template-columns: 1fr;
    width: 50%;
    margin-inline: auto;
  }
}

.pie-charts-container #pie-chart-container-nbbikes, .pie-charts-container #pie-chart-container-nbemptydocks, .pie-charts-container #pie-chart-container-nbdocks {
  aspect-ratio: 1;
  width: 100%;
  margin-inline: auto;
}

